.componentChild {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: linear-gradient(
    90deg,
    #514fb1,
    #4c75b7 24.3%,
    #359fad 77.57%,
    #2cafa8 98.44%
  );
  backdrop-filter: blur(4px);
  opacity: 0.9;
}
.componentItem {
  position: absolute;
  height: 44.55%;
  width: 12.63%;
  top: 31.68%;
  right: 4.7%;
  bottom: 23.76%;
  left: 82.67%;
  border-radius: var(--br-9xs);
  background-color: var(--color-yellow);
}
.requestContact {
  position: absolute;
  height: 19.8%;
  width: 10.12%;
  top: 43.56%;
  left: 83.93%;
  text-transform: uppercase;
  font-weight: 500;
  color: var(--color-gray);
  display: flex;
  align-items: center;
  justify-content: center;
}
.home {
  cursor: pointer;
}
.home,
.howItWorks {
  position: relative;
  text-transform: uppercase;
  font-weight: 500;
}
.homeParent {
