  align-items: center;
  justify-content: center;
  gap: var(--gap-62xl);
  font-size: var(--font-size-base);
}
.componentInner {
  position: absolute;
  height: 58.81%;
  width: 3.4%;
  top: 23.86%;
  right: 91.83%;
  bottom: 17.33%;
  left: 4.77%;
