}
.offfferwall {
  margin: 0;
}
.monetization {
  margin: 0;
  color: var(--color-yellow);
}
.offfferwallMonetization {
  position: absolute;
  top: 21.78%;
  left: 8.86%;
  font-size: var(--font-size-11xl);
  line-height: 0%;
